.contacts-section {
  padding-bottom: 70px;
  @include m-b-tablet-down {
    padding-bottom: 0;
  }
    &__list {
        @extend .list-reset;
        display: flex;
        align-items: center;
        padding: 50px 0;
        div {
            display: block;
            width: 3px;
            height: 3px;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            background: #05376c;
            margin: 0 4px;
        }
    }
    h2 {
        display: flex;
        justify-content: flex-start;
    }
   &__maps {
    position: relative;
        &-adress {
            @extend .list-reset;
            position: absolute;
            background: white;
            top: 0;
            left: 50px;
            width: 28%;
            height: 100%;
            justify-content: center;
            display: flex;
            flex-direction: column;
            gap: 30px;
            @include m-b-tablet-down  {
                position: relative;
                left: 0;
                height: auto;
                width: auto;
                padding: 43px 16px
            }
            li {
                display: flex;
                gap: 10px;
                padding-left: 10px;
                div {
                    display: flex;
                    flex-direction: column;
                }
                span {
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 20px;
                    color: #111;
                }
                a {
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 20px;
                    color: #111;
                }
                h3 {
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 24px;
                    color: #05376c;
                    text-transform: uppercase;
                    margin: 0;
                    padding-bottom: 5px;
                }
            }
        }
   }
}
